body {
  margin: 0;
  padding: 0;
}

div {
  display: inline-block;
  margin: 0;
}

a {
  text-decoration: none;
}

.app {
  width: 450px;
  height: 275px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 18px 10px;
  box-sizing: border-box;
  border-bottom: 1px solid lightgrey;
}

h1 {
  font-family: Raleway;
  font-size: 17px;
  color: #2867B2;
  margin: 0;
}

.main {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 18px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup-text {
  text-align: center;
  font-size: 14px;
  width: 250px;
}

.error {
  color: rgb(161, 41, 41);
  font-weight: 600;
}

.results-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 15px;
}

h2 {
  text-align: center;
}

.name-text {
  color: #2867B2;
}

.company-text {
  color: green;
}

.email-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.email-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 120px;
}

.email-buttons > div, .email-buttons > a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: white;
  border-radius: 10%;
  border: 1px solid lightgrey;
  cursor: pointer;
}

.popup {
  position: fixed;
  top: 50%;
  left: 26%;
  padding: 1rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5%;
}

.lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: lightgrey;
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.lds-default div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.lds-default div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.lds-default div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.lds-default div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.lds-default div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.lds-default div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.lds-default div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.lds-default div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.lds-default div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.lds-default div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.lds-default div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%, 20%, 80%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}